@font-face {
  font-family: "Geometria";
  font-style: bold;
  font-weight: 700;
  src: local("Geometria"),
    url('font/geometria_bold.otf') format("opentype")
}

@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 500;
  src: local("Geometria"),
    url('font/geometria_medium.otf') format("opentype")
}

.calculator-app {
  font-family: Geometria;
  background-color: rgba(255, 255, 255, 0.2);
  color: rgba(242, 242, 242, 1);
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  min-width: 240px;
  min-height: 320px;

}

.calculator {
  background: linear-gradient(155.23deg, #28518E 0%, #3A77D1 100%);
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

.calculator-input-container {
  margin: 0 auto;
  width: 83%;
  max-height: 100%;
  padding-top: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.keyboard-container {
  width: 100%;
  height: 58%;
}

 .input-display {
  text-align: right;
  background: none;
  border: none;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  height: auto;
  min-height: 40px;
  font-size: 2.333em;
  line-height: 1.43;
  font-weight: 700;
  line-height: 1.43;
  overflow-x: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
} 

.expression-text::-webkit-scrollbar {
  display: none;
} 

/* пограничные размеры: 
240 на 320
640 на 640
640 на 1024
240 на 1024 */

@media (max-width: 40em) and (orientation: portrait) {
  .calculator-app {
    border-radius: 0;
  }

}

/* минимальные размеры на компьютере: 
320 на 320
640 на 320*/

@media (max-width: 60em) and (orientation: landscape) {
  .calculator-app {
    border-radius: 0;
  }

}

/* минимальные размеры на компьютере: 
960 на 320
640 на 320*/

@media ((min-width: 40em) and (orientation: portrait)) or (min-width: 60em) {
  .calculator-app {
    height: 95vh;
    max-width: 616px;
    max-height: 876px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 18px;
    padding: 38px 31px 38px 31px;
  }

  .calculator {
    box-shadow: 0px 3.71px 7.16px 0px rgba(0, 0, 0, 0.1219),
    0px 10.27px 19.78px 0px rgba(0, 0, 0, 0.175),
    0px 24.72px 47.63px 0px rgba(0, 0, 0, 0.2281),
    0px 82px 158px 0px rgba(0, 0, 0, 0.35);
    border-radius: 18px;

  }
}


